import {Component, OnInit, Renderer2, ElementRef, ContentChildren, QueryList, AfterContentInit, OnChanges, AfterViewInit} from "@angular/core";


@Component({
  moduleId: module.id,
  selector: "fs-nav",
  templateUrl: "./nav.component.html",
  styleUrls: ["./nav.component.scss"]
})


export class NavComponent implements AfterViewInit {
  constructor(public el: ElementRef, public render: Renderer2) {
    this.element = el.nativeElement;
  }
  public element:HTMLElement;
  public selectedIndex:number = 1;

  public link: any;
  public linkActive: any;
  public nav:any;
  public navLine:any;
  public navCoords:any;

  getElements() {
    this.link = [].slice.call(this.element.querySelectorAll('.nav__link'));
    this.nav = this.element.querySelector('.nav__list');
    this.navCoords = this.nav.getBoundingClientRect();
    this.navLine  = this.render.createElement('li');
    this.render.addClass(this.navLine, 'nav__line');
    this.render.appendChild(this.nav, this.navLine);
  }

  ngAfterViewInit() {
    this.getElements();
      this.link.map( item => {
        if (item.classList.contains('nav__link--active')) {
          this.render.setStyle(this.navLine, "left",  item.offsetLeft + "px" );
          this.render.setStyle(this.navLine, "width",  item.clientWidth + "px" );
        }
      })
  }

  setActiveClass(index) {
    this.selectedIndex = index;
    console.log(this.link[index].offsetLeft);
    this.render.setStyle(this.navLine, "left",  this.link[index].offsetLeft + "px" );
    this.render.setStyle(this.navLine, "width", this.link[index].clientWidth + "px");
  }

  public navList: CheckNavList[] = [
    {item: "Home"},
    {item: "About"},
    {item: "Description"},
    {item: "Developer" },
    {item: "Contacts"},

  ];
}

export class CheckNavList {
  item: string;
}



