import { Component, OnInit } from '@angular/core';
import {AngularFire, AuthProviders, AuthMethods,FirebaseListObservable } from "angularfire2";


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent {
  title = 'app works!';

  items: FirebaseListObservable<any>;
  name: any;
  msgVal: string = '';
  displayName: string = "";
  emailCurrent: string = "";
  photoCurrent: string = '';

  constructor(public af: AngularFire) {
    this.items = af.database.list('/messages', {
      query: {
        limitToLast: 5
      }
    });

    this.af.auth.subscribe(auth => {
      if (auth) {
        this.name = auth;
        this.emailCurrent = auth.facebook.email;
        this.displayName = auth.facebook.displayName;
        this.photoCurrent = auth.facebook.photoURL;

      }
    });

  }

  login() {
    this.af.auth.login({
      provider: AuthProviders.Facebook,
      method: AuthMethods.Popup,
    });
  }

  chatSend(theirMessage: string) {
    this.items.push({ message: theirMessage, name: this.name.facebook.displayName});
    this.msgVal = '';
    console.log( this.af.database.list);
  }

  logOut() {
    this.af.auth.logout();
  }




  publicClasses = {
    dropdown: ".dropdown",
    dropdownButton: "dropdown__button",
    dropdownList: "dropdown__link"

  };

  ngOnInit() {

  }
}



