import {Component, OnInit} from "@angular/core";


@Component({
  moduleId: module.id,
  selector: "todo",
  templateUrl: "./todo.component.html",
  styleUrls: ["./todo.component.scss"]
})

export class TodoComponent implements OnInit {
  title:string = "my todo app ";
  todoModel:string;
  keyName:string = 'tasks';
  toggle: boolean = false;


  public tasks:Array<any> = [
    {description: "lorem ipsum", time: 1970}
  ];


  ngOnInit() {
    this.checkItems();
  }

  toggleFunc() {
    this.toggle = !this.toggle;
  }


  checkItems() {
    let task = this.getFromLocalStorage(this.keyName);
    this.tasks = JSON.parse(task);
  }

  setToLocalStorage(name:string, items:Array<any>) {
    localStorage.setItem(name, JSON.stringify(items));
  }

  getFromLocalStorage(name:string) {
      return localStorage.getItem(name);
  }

  sortTasks() {
    this.toggleFunc();
    if (!!this.toggle) {
      this.tasks.sort((a, b) => {
        if (a.description < b.description) {
          return -1;
        } else {
          return 1;
        }
      });
    } else if(!this.toggle) {
      this.tasks.sort((a, b) => {
        if (a.description < b.description) {
          return 1;
        } else {
          return -1;
        }
      });
    }


    return this.tasks;
  }


  addTask(item) {
    if (item) {
      this.tasks.push({description: item, time: Date.now()});
      this.setToLocalStorage('tasks', this.tasks);
    }

    this.todoModel = ''
  }


  removeTask(event, index) {
    event.preventDefault();
    this.tasks.splice(index, 1);
    this.setToLocalStorage(this.keyName, this.tasks);
  }
}



